import useLoginPageStyles from './LoginPage.style'
import {ReactChild, FC, useState} from 'react'
import {Box, Button, Input, TextField} from '@mui/material'
import Page from "@/components/Page/Page";
import * as React from "react";
import Login from "@/api/login/login";


const {log} = console

interface LoginPageProps {

    children?: ReactChild,

}


const login = new Login()

const LoginPage: FC<LoginPageProps> = ({children}) => {

    const Styles = useLoginPageStyles()

    const [username, setUsername] = useState('jjjjj')



    function handleChange(event: any){
        setUsername(event.target.value)

    }

    async function handleClick(){
        await login.sendCode('test')

    }

    return (
        <Page>

            <Box component={Styles} className="LoginPage">

                <Box
                    component={"form"}
                    className='login-page__login-form login-form'
                    noValidate
                    autoComplete="off"
                >

                    <TextField
                        id="outlined-controlled"
                        label="Controlled"
                        value={username}
                        onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                            setUsername(event.target.value);
                        }}
                    />
                    <Button
                        disabled={username === ''}
                        variant={'contained'}
                        onClick={handleClick}


                    ></Button>

                </Box>

            </Box>

        </Page>
    )
}
export default LoginPage